.Header {
    width: 100%;
    height: 500px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.img_block {
    width: 38%;
    height: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    position: relative;
}

.img_block img {
    width: 60%;
    height: 90%;
    border-radius: 30px;
    box-shadow: 0px 0px 14px 2px black;
    margin-left: 30px;
    z-index: 2;
}

.img_block::after {
    content: ''; 
    position: absolute;
    top: 15%;
    right: 50%;
    width: 100%;
    height: 85%;
    background-color: #efad02; 
    clip-path: polygon(0 0, 80% 0, 100% 100%, 0% 100%);
    z-index: 1;
}

.title_block {
    height: 100%;
    width: 55%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.title_block .title {
    height: 40%;
    display: flex;
    align-items: center;
    color: #efad02;
    position: relative;
    padding-left: 40px;
}

.title_block .title::after {
    content: "";
    height: 3px;
    width: 30px;
    position: absolute;
    background-color: #efad02;
    top: 80px;
    left: 0;
}

.title_block .title span {
    color: whitesmoke;
}

.title_block .text {
    height: 40%;
    width: 70%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    color: whitesmoke;
    line-height: 2;
}

@media (min-width: 320px) and (max-width: 500px) {
    .Header {
        flex-direction: column;
        height: auto;
    }

    .img_block {
        width: 100%;
        justify-content: center;
    }

    .img_block img {
        width: 80%;
    }

    .title_block {
        width: 100%;
        align-items: center;
    }

    .title_block .title {
        text-align: center;
        padding-left: 0;
        line-height: 1.7;
        font-size: 100%;
    }
    
    .title_block .title::after {
        display: none;
    }

    .title_block .text {
        width: 100%;
        text-align: center;
        align-items: center;
        font-size: 100%;
    }
    
    .title_block .text p {
        margin-bottom: 40px;
    }
}

@media (min-width: 501px) and (max-width: 768px) {
    .Header {
        flex-direction: column;
        height: auto;
    }

    .img_block {
        width: 100%;
        justify-content: center;
    }

    .img_block img {
        width: 50%;
    }

    .title_block {
        width: 100%;
        align-items: center;
    }

    .title_block .title::after {
        content: "";
        height: 3px;
        width: 30px;
        position: absolute;
        background-color: #efad02;
        top: 50px;
        left: -40px;
    }

    .title_block .title {
        text-align: center;
        padding-left: 0;
        line-height: 1.7;
        font-size: 100%;
    }
    
    .title_block .text {
        width: 100%;
        text-align: center;
        align-items: center;
        font-size: 20px;
    }
    
    .title_block .text p {
        margin-bottom: 40px;
        padding-left: 5px;
        padding-right: 5px;
    }
}

@media (min-width: 769px) and (max-width: 1024px) {
    .img_block img {
        width: 100%;
    }

    .title_block .text {
        width: 80%;
    }
}